.container {
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;

  @media screen and (min-width: 480px) {
    max-width: 480px;
    // margin: 0 auto;
  }

  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding-left: 34px;
    padding-right: 34px;
  }

  @media screen and (min-width: 1200px) {
    max-width: 1200px;
    padding-left: 115px;
    padding-right: 115px;
  }
}